"use client";

import { useEffect } from "react";
import { MapContainer, TileLayer, Marker, Popup, useMap } from "react-leaflet";
import L, { Icon, LatLngTuple } from "leaflet";
import "leaflet/dist/leaflet.css";
import { Geocode } from "@/src/types/Geocode";

interface Coordinates {
  lat: number;
  lon: number;
}

// Icons with diferent colors
const blueIcon: Icon = new L.Icon({
  iconUrl: "https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-blue.png",
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
});
const redIcon: Icon = new L.Icon({
  iconUrl: "https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-red.png",
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
});
const greenIcon: Icon = new L.Icon({
  iconUrl: "https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-green.png",
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
});

const jugadorCoors: Coordinates = { lat: 37, lon: -4 };
const listJugadores: Coordinates[] = [{ lat: 37.5, lon: -3.5 }, { lat: 36.5, lon: -4.5 }];
const listPokemon: Coordinates[] = [{ lat: 37.5, lon: -4.5 }, { lat: 36.5, lon: -3.5 }];

function FlyToLocation({ lat, lng }: { lat: number; lng: number }) { //si quiero lat y lng de algo, algo.lat, algo.lng
  const map = useMap();
  useEffect(() => {
    map.flyTo([lat, lng], 10); //El numero indica el aumento de zoom con 1 se ve toda la tierra.
  }, [lat, lng, map]);
  return null;
}

export default function OSMMap() {
  return (
    <MapContainer center={[jugadorCoors.lat, jugadorCoors.lon]} zoom={15} style={{ width: "600px", height: "400px", borderRadius: "8px", margin: "0 auto" }}>
      <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>' />
      {listPokemon.map((pokemon, index) => (
        <Marker key={index} position={[pokemon.lat, pokemon.lon]} icon={blueIcon} >
          <Popup>{pokemon.lat}{pokemon.lon}</Popup>
        </Marker>
      ))}
      {listJugadores.map((jugador, index) => (
        <Marker key={index} position={[jugador.lat, jugador.lon]} icon={greenIcon} >
          <Popup>{jugador.lat}{jugador.lon}</Popup>
        </Marker>
      ))}
      <Marker position={[jugadorCoors.lat, jugadorCoors.lon]} icon={redIcon} >
        <Popup>{jugadorCoors.lat}{jugadorCoors.lon}</Popup>
      </Marker>
      <FlyToLocation lat={jugadorCoors.lat} lng={jugadorCoors.lon} />
    </MapContainer>
  );
}
